@import 'mixins';
@import 'variables';

.full {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #f1f3f6;
}

.login-wrap {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    max-width: 340px;
    margin: -170px auto 0;
    text-align: center;
    color: $brand-primary;
}

.login-logo {
    margin: 20px auto;
}

.login-form {
    margin: 25px auto 40px
}

.login-form .help-block,
.login-form .parsley-errors-list {
    text-align: left
}

.form-title {
    font-size: 16px;
    font-weight: 700;
    text-align: center
}

.form-submit {
    border-top: 1px solid #bfbfbf;
    margin: 20px 0 15px;
    padding-top: 15px
}

.form-submit:after, .form-submit:before {
    content: " ";
    display: table
}

.form-submit:after {
    clear: both
}

.form-submit button {
    font-size: 17px;
    font-weight: 700
}

.btn_cancel, .btn_join {
    width: 49%
}

.btn_cancel {
    float: left
}

.btn_join {
    float: right
}

.form-helper {
    display: block;
    font-size: 13px;
    font-weight: 400;
    text-align: right
}

.form-helper:focus, .form-helper:hover {
    text-decoration: underline
}

.form-submit + .form-helper {
    position: relative;
    text-align: center
}

.form-submit + .form-helper:after, .form-submit + .form-helper:before {
    height: 1px;
    position: absolute;
    top: 10px;
    width: 20%;
    background-color: #bfbfbf;
    content: ""
}

.form-submit + .form-helper:before {
    left: 15%
}

.form-submit + .form-helper:after {
    right: 15%
}
