/** font-family **/
// $font-family-base: 'Helvetica', 'Arial', 'Nanum Gothic', sans-serif;
// Case1. 브라우저 타입별(엣지포함) 적용 또는 사용자 시스템 폰트 활용
$font-family-base: 'Open Sans', 'Arial', Dotum, sans-serif;
// Case2. 사용자 PC에 설치된 로컬 폰트를 최대한 사용
//$font-family-base: 'Arial', 'Open Sans', 'Nanum Barun Gothic', '나눔바른고딕', 'Nanum Gothic', '나눔고딕', Dotum, sans-serif;
$font-family-extra: 'Roboto', 'Helvetica', 'Arial', sans-serif;

/* base colors */
$shadow:        0 2px 4px 0 rgba(0, 0, 0, 0.16), 0 2px 8px 0 rgba(0, 0, 0, 0.12);

/* Font size */
$font-size-0: 13px !default;    // Font0 : Table Text
$font-size-1: 14px !default;    // Font1 : Base
$font-size-2: 15px !default;    // Font2 : Table Title
$font-size-3: 16px !default;    // Font3 : Btns
$font-size-4: 18px !default;    // Font4 : Popup Title, Main Menu Text
$font-size-5: 20px !default;    // Font5 : Main Title Info, Wizard Step
$font-size-6: 24px !default;    // Font6 : Wizard Step Title

$gdkm-color-1st: #ffad00;
//$gdkm-color-2nd: #ffd681;
//$gdkm-color-3rd: #ffd681;

// bootstrap-sass path
$icon-font-path: '../../vendors/bootstrap-sass/assets/fonts/bootstrap/';



//== Colors
//
//## Gray and brand colors for use across Bootstrap.
$gray-base:              #000 !default;
$gray-darker:            lighten($gray-base, 13.5%) !default; // #222
$gray-dark:              lighten($gray-base, 20%) !default;   // #333
$gray:                   lighten($gray-base, 33.5%) !default; // #555
$gray-light:             lighten($gray-base, 46.7%) !default; // #777
$gray-lighter:           lighten($gray-base, 93.5%) !default; // #eee

//$brand-primary:         darken(#ffad00, 6.5%) !default; // #337ab7
$brand-primary:         #c18300 !default;
$brand-secondary:       #ffad00 !default;
$brand-success:         #00a99d !default;
$brand-info:            #3498db !default;
$brand-warning:         #f8a532 !default;
$brand-danger:          #e74c3c !default;


//== Scaffolding
//
//## Settings for some of the most global styles.

//** Global text color on `<body>`.
$text-color:            $gray-dark !default;

//** Global textual link color.
$link-color:            #555 !default;


//== Components
//
//## Define common padding and border radius sizes and more. Values based on 14px text and 1.428 line-height (~20px to start).

$border-radius-base:        0 !default;
$border-radius-large:       0 !default;
$border-radius-small:       3px !default;


//== Navs
//
//## Navs for use across Bootstrap.

//=== Shared nav styles
$nav-link-padding:                          12px 15px !default;

//== Tabs
$nav-tabs-border-color:                     #cccccc !default;
$nav-tabs-active-link-hover-border-color:   #cccccc !default;
$nav-tabs-justified-link-border-color:            #cccccc !default;


//== Forms
//
//##

//** Text color for `<input>`s
$input-color:                    $gray !default;
//** `<input>` border color
$input-border:                   #cccccc !default;
