// --------------------------------------------------
// Box sizing
// --------------------------------------------------

@mixin box-sizing($box-model) {
  -webkit-box-sizing: $box-model; // Safari <= 5
     -moz-box-sizing: $box-model; // Firefox <= 19
          box-sizing: $box-model;
}

// --------------------------------------------------
// Clearfix
// --------------------------------------------------

// ex: @include clearfix;
@mixin clearfix() {
    &:before,
    &:after {
        content: ' ';
        display: table;
    }
    &:after {
        clear: both;
    }
}

// --------------------------------------------------
// Positioning
// --------------------------------------------------

// ex: @include position(absolute, $top: 5px, $left: 5px);
@mixin position($position, $top: null, $right: null, $bottom: null, $left: null) {
    position: $position;
    top: $top;
    right: $right;
    bottom: $bottom;
    left: $left;
}

// --------------------------------------------------
// Sizing
// --------------------------------------------------

// ex 2: @include size(100%, 1px); */
@mixin size($width, $height) {
    width: $width;
    height: $height;
}


// --------------------------------------------------
// Font
// --------------------------------------------------

// ex: @include font($size: 14px, $style: italic, $weight: 400, $variant: normal, $family: $font-family-base);
@mixin font($size: null, $style: null, $weight: null, $variant: null, $family: null) {
    font-size: $size;
    font-style: $style;
    font-weight: $weight;
    font-variant: $variant;
    font-family: $family;
}


// --------------------------------------------------
// Text Overflow
// --------------------------------------------------

/* 한 줄 자르기 */
@mixin text-overflow {
    display: inline-block;
    text-overflow: ellipsis;
    white-space: nowrap;
}

/* 여러 줄 자르기 스타일 (multiline) */
// ex: @include multiLineEllipsis($lineHeight: 1.2em, $lineCount: 3, $bgColor: white);
@mixin multiLineEllipsis($lineHeight: 1.2em, $lineCount: 1, $bgColor: white){
    overflow: hidden;
    position: relative;
    line-height: $lineHeight;
    max-height: $lineHeight * $lineCount;
    text-align: justify;
    margin-right: -1em;
    padding-right: 1em;

    &:before {
        content: '...';
        position: absolute;
        right: 0;
        bottom: 0;
    }

    &:after {
        content: '';
        position: absolute;
        right: 0;
        width: 1em;
        height: 1em;
        margin-top: 0.2em;
        background: $bgColor;
    }
}


// --------------------------------------------------
// DataTable
// --------------------------------------------------

@mixin dataTable-height($td-height, $tr-num, $thead_height, $footer-height) {
    height: $thead_height + $footer-height + ($td-height * $tr-num);
}


// --------------------------------------------------
// Placeholder
// --------------------------------------------------

// ex: @include placeholder($color-base);
@mixin placeholder($color: $input-color-placeholder) {
    &::-moz-placeholder           { color: $color; } // Firefox 19+
    &:-ms-input-placeholder       { color: $color; } // Internet Egplorer 10+
    &::-webkit-input-placeholder  { color: $color; } // Safari and Chrome
}


// --------------------------------------------------
// RGBA Background Opacity
// --------------------------------------------------

// ex: @include bg-opacity($color-dark, .2);
@mixin bg-opacity($color, $opacity: 0.3) {
    background: rgba($color, $opacity);
}

// --------------------------------------------------
// Cubic Bezier Transition
// --------------------------------------------------

// ex: @include cubic-transition($delay: 0, $duration: 300ms, $property: (all));
@mixin cubic-transition($delay, $duration, $property) {
    transition: {
        duration: $duration;
        property: $property;
        timing-function: cubic-bezier(.7,1,.7,1);
    }
}

// --------------------------------------------------
// Rotate
// --------------------------------------------------

/* ex: @include rotate(45deg); */
@mixin rotate($degrees) {
    -webkit-transform: rotate($degrees);
    -moz-transform: rotate($degrees);
    transform: rotate($degrees);
}

// --------------------------------------------------
// Translate 3D Transition
// --------------------------------------------------

/* ex: @include translate3d(0,0,0); */
@mixin translate3d($x, $y, $z) {
    -webkit-transform: translate3d($x, $y, $z);
    -moz-transform: translate3d($x, $y, $z);
    transform: translate3d($x, $y, $z);
}

// --------------------------------------------------
// Border Radius
// --------------------------------------------------

/* ex: @include border-radius(3px); */
@mixin border-radius($radius) {
    border-radius: $radius;
}

// Single side border-radius
@mixin border-top-radius($radius) {
    -webkit-border-top-right-radius: $radius;
    border-top-right-radius: $radius;
    -webkit-border-top-left-radius: $radius;
    border-top-left-radius: $radius;
    background-clip: padding-box;
}
@mixin border-right-radius($radius) {
    -webkit-border-bottom-right-radius: $radius;
    border-bottom-right-radius: $radius;
    -webkit-border-top-right-radius: $radius;
    border-top-right-radius: $radius;
    background-clip: padding-box;
}
@mixin border-bottom-radius($radius) {
    -webkit-border-bottom-right-radius: $radius;
    border-bottom-right-radius: $radius;
    -webkit-border-bottom-left-radius: $radius;
    border-bottom-left-radius: $radius;
    background-clip: padding-box;
}
@mixin border-left-radius($radius) {
    -webkit-border-bottom-left-radius: $radius;
    border-bottom-left-radius: $radius;
    -webkit-border-top-left-radius: $radius;
    border-top-left-radius: $radius;
    background-clip: padding-box;
}
